footer {
  backdrop-filter: blur(5px);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer__social {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer__social a {
  font-weight: 5rem;
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid var(--color-primary-variant);
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-light);
}
